import React from 'react'
import Link from 'next/link'
import fetch from 'isomorphic-unfetch'
import Masonry from 'react-masonry-component'

const masonryOptions = {
  transitionDuration: 0
}

export default class Gallery extends React.Component{
  constructor(props){
    super(props)
    this.state = {
      media: []
    }
    this.fetchImages = this.fetchImages.bind(this)
  }

  async fetchImages(){
    const res = await fetch('https://images-api.nasa.gov/search?q=globe')
    const data = await res.json()
    this.setState({
      media: data.collection.items
    })
    console.log(`Media data fetched. Count: ${data.collection.items.length}`)
  }

  componentWillMount() {
    this.fetchImages()
  }

  render(){
    <style jsx>{`
      .image-element-class {
        color: blue;
      }
    `}</style>
    return(
      <div>
        <h1>GLOBE Media Gallery</h1>
        <Masonry
          className={'my-gallery-class'} // default ''
          // elementType={'ul'} // default 'div'
          options={masonryOptions} // default {}
          disableImagesLoaded={false} // default false
          updateOnEachImageLoad={false} // default false and works only if disableImagesLoaded is false
        >
            {this.state.media.map(medium => (
              <div
                key={medium.data[0].nasa_id}
                className="image-element-class">
                <p className="image-element-class">hello</p>
                <img src={medium.links[0].href} />
              </div>
            ))}
        </Masonry>
      </div>
    )
  }
}



